module app {
	'use strict';

	class BookController {

		books: IBook[] = null;
		author: string;

		static $inject = ['$routeParams','app.dataService'];		
		constructor($routeParams, dataService: DataService) {

			this.books = $routeParams.books;

			dataService.getBooks()
				.then((books: IBook[]) => {
					this.books = books;
				});
		}
	}

	angular.module('app')
        .controller('app.BookController', BookController);
}
